import { Link } from 'react-scroll'
// import skillsIcon from '../assets/img/skills.svg'
import projectsIcon from '../assets/img/briefcase.svg'
import contactIcon from '../assets/img/information.svg'
import { ContactMe } from './ContactMe'

export function Navbar() {
  return (
    <header className="flex column justify-center align-center header-container">
      <nav>
        <ul className="clean-list flex">
        <li className="flex column align-center">
            <Link
              to="hero"
              className="nav-link"
              activeClass="active"
              spy={true}
            >
              <img className="nav-icon" src={contactIcon} alt="" />
              <p>who am i</p>
            </Link>
          </li>
          <li className="flex column align-center">
            <Link
              to="projects"
              className="nav-link"
              activeClass="active"
              spy={true}
            >
              <img className="nav-icon" src={projectsIcon} alt="" />
              <p>projects</p>
            </Link>
          </li>
          {/* <li className="flex column align-center">
            <Link
              to="about"
              className="nav-link"
              activeClass="active"
              spy={true}
            >
              <img className="nav-icon" src={skillsIcon} alt="" />
              <p>skills</p>
            </Link>
          </li> */}

        </ul>
      </nav>
      <div className="hover-bg"></div>
      <ContactMe />
    </header>
  )
}
