import LazyAnimation from './LazyAnimation'

export function ProjectPreview({ project, idx }) {
  const openDemo = () => {
    window.open(project.demoUrl, '_blank')
  }

  const openRepo = () => {
    window.open(project.repoUrl, '_blank')
  }

  return (
    <LazyAnimation>
      <div className="flex column align-center project-preview">
        <section>
          <section className="project-details">
            <div
              className="container-2"
              style={
                idx % 2 === 0
                  ? { borderRadius: '100px 20px 20px 20px' }
                  : { borderRadius: '20px 20px 100px 20px' }
              }
            >
              <p className="number">{idx + 1}</p>
              <h2>{project.name}</h2>
              <p className="text">{project.description}</p>
              <section className="project-actions">
                <button className="simple-button" onClick={() => openDemo()}>
                  Visit Project
                </button>
                <button className="simple-button" onClick={() => openRepo()}>
                  Visit Source
                </button>
              </section>
            </div>
          </section>
        </section>
      </div>
    </LazyAnimation>
  )
}
